import { h, Component } from 'preact';
import { observer } from 'mobx-preact';

import LineGraph from '../../components/lineGraph';
import wutil from '../../lib/wifet-util';

const forcastData = wutil.makeRandomData({ cnt: 500, min: 5, max: 15, onlyUp: false, inputData: [], diff: 1 });
const aiData = wutil.makeRandomData({ cnt: 500, min: 0, max: 30, onlyUp: false, inputData: forcastData, diff: 1, maxSkew: 10 });
const windData = wutil.makeRandomData({ cnt: 500, min: 0, max: 30, onlyUp: false, inputData: aiData, diff: 1, maxSkew: 10 });

const initialState = {
	forcastData,
	aiData,
	windData,
};

@observer
class Weather extends Component {
    constructor(props) {
        super(props);
        this.state = {
            ...initialState,
        };
    }

	render() {
		const { windData, forcastData, aiData } = this.state;
		return (
			<div class='container-fluid mt-5 pt-2'>
				<div class='row'>
					<div class='col-12'>
						<h1>Weather</h1>
						<LineGraph
							stores={this.props.stores}
							data={windData}
							dataB={forcastData}
							dataC={aiData}
							legend='Wind'
							legendB='Wind forcast'
							legendC='Wind AI prediction'
							width={1200}
							height={500}
							yMax={30}
							yMin={0}
							showYTicks={1}
							yTicks='["30 m/s", "25 m/s", "20 m/s", "15 m/s", "10 m/s", "5 m/s", "0 m/s"]'
							unit='m/s'
							showXTicks={1}
							marker={400}
						/>
					</div>
				</div>
			</div>
		);
	}
}

export default Weather;
